var routeEnter = require('routeEnter');

module.exports = Object.assign({
    path: "list",
    breadcrumbName: "找项目",
    getComponent: function (location, cb) {
        require.ensure([], function (require) {
            cb(null, require("./components/frame.jsx"))
        }, 'fundList')
    },
    getIndexRoute(nextState, cb) {
        require.ensure([], function(require) {
            cb(null, {
                breadcrumbName: '',
                component: require("./components/list.jsx")
            })
        }, 'index')
    },
    getChildRoutes(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./routes/debts"),
                require("./routes/stock"),
                require("./routes/transfer")
            ])
        })
    }
}, routeEnter);


