var routeEnter = require('routeEnter');

module.exports = Object.assign({
    path: "space",
    breadcrumbName: "项目方空间",
    getComponent: function (location, cb) {
        require.ensure([], function (require) {
            cb(null, require("./components/spaceFrame.jsx"))
        }, 'projectSpaceFrame')
    },
    getIndexRoute(nextState, cb) {
        require.ensure([], function(require) {
            cb(null, {
                breadcrumbName: '账户总览',
                component: require("./routes/overview/overview.jsx")
            })
        }, 'projectSpaceOverview')
    },
    getChildRoutes(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./routes/recharge"),
                require("./routes/info"),
                require("./routes/auth"),
                require("./routes/draft"),
                require("./routes/published"),
                require("./routes/deliver"),
                require("./routes/feedback"),
                require("./routes/seen"),
                require("./routes/favorite"),
                ...require("./routes/subscribe"),
                require("./routes/publish")
            ])
        })
    }
}, routeEnter);


